<template>
  <div class="hello">
    <b-img src="../assets/logo.png" fluid alt="Fluid image" class="mb-5 p-5"/>
    <b-card title="授权页面"
          tag="article"
          class="mb-2">
        <p class="card-text">

        </p>
        <b-form action="/api/oauth/logout" method='post' >
            <b-button type="submit" variant="primary">重新绑定</b-button>
        </b-form>
    </b-card>
  </div>
</template>

<script>

export default {
  name: 'Login',
  data () {
    return {
      form: {
        phone: '',
        code: ''
      },
      requestInterval: 0,
      backendError: ''
    }
  },

  methods: {
    onSubmit (evt) {
      this.$http.post('/api/oauth/logout')
    },
    onReset (evt) {
      evt.preventDefault()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

a {
  color: #42b983;
}

.my-row {
  margin-bottom: 20px
}

</style>



